import Cookies from 'js-cookie';

/**
 *  Cookie 
 * @param {Object} info
 * @returns {boolean}
 */
export function setCookie(info) {
  //  
  // {key1:value1,key2,value2} => [[key1,value1],[key2,value2]]
  const arr = Object.entries(info);
  for (let i = 0; i < arr.length; i += 1) {
    Cookies.set(arr[i][0], arr[i][1]);
  }
  return true;
}

/**
 *  cookies user
 */
export function getUserCookie() {
  return {
    username: Cookies.get('username'),
    appkey: Cookies.get('appkey'), // 
    role: Cookies.get('role'), //   
    phoneNumber: Cookies.get('phoneNumber'),
    avatarUrl: Cookies.get('avatarUrl'), // 
    userIDNumber: Cookies.get('userIDNumber'), // 
    userRealName: Cookies.get('userRealName'), // 
    userBirthday: Cookies.get('userBirthday'), // 
    userMoney: Cookies.get('userMoney'), // 
    userWorkTime: Cookies.get('userWorkTime'), // 
  };
}

/**
 * cookies
 * @returns {boolean}
 */
export function removeUserCookie() {
  Cookies.remove('appkey');
  Cookies.remove('username');
  Cookies.remove('role');
  Cookies.remove('phoneNumber');
  Cookies.remove('avatarUrl');
  Cookies.remove('userIDNumber');
  Cookies.remove('userRealName');
  Cookies.remove('userBirthday');
  Cookies.remove('userMoney');
  Cookies.remove('userWorkTime');
  return true;
}
